<template>
  <cover>
    <div class="resume-data-picker" ref="resumeDatePicker">
      <datetime-picker
        type="year-month"
        @confirm="handleConfirm"
        @cancel="$emit('hide')"
      />
    </div>
  </cover>
</template>

<script>
import cover from "./cover";
import { DatetimePicker } from "vant";
export default {
  model: {
    prop: "date",
    event: "changeDate"
  },
  props: ["date"],
  components: {
    cover,
    DatetimePicker
  },
  methods: {
    handleConfirm(date) {
      this.$emit("changeDate", date);
      this.$emit("hide");
    }
  }
};
</script>

<style lang="postcss" scoped>
.resume-data-picker {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
</style>




