<template>
  <div class="resume-new-education">
    <cell-group class="resume-new-education__item">
      <field
        label="学校"
        placeholder="学校名"
      />
      <field
        label="专业"
        placeholder="专业名"
      />
      <field
        label="学历"
        placeholder="学历"
      />
      
      <field
        label="开始时间"
        readonly
        :value="startTime | formatDate"
        @click="isShowStartTime=true"
      />
      <field
        label="结束时间"
        readonly
        placeholder="请选择"
        :value="endTime | formatDate"
        @click="isShowEndTime=true"
      />

      <datePicker
        v-model="startTime"
        v-if="isShowStartTime"
        @hide="isShowStartTime = false"  
      />
      <datePicker
        v-model="endTime"
        v-if="isShowEndTime"
        @hide="isShowEndTime = false"  
      />
    </cell-group>
    <cell-group class="resume-new-education__item">
      <cell title="学校LOGO(可选)" center>
        <uploader>
          <img v-if="logoUrl" class="upload-img" src="/resume-card/static/image/avatar.jpeg"/>
          <Icon v-else name="photograph" size="13.333vw" />
        </uploader>
      </cell>
    </cell-group>

    <div class="resume-new-education__button">
      <my-button size="large" type="primary">保存</my-button>
    </div>
  </div>  
</template>

<script>
import datePicker from "@/components/date-picker";
import dayjs from "dayjs";
import { Cell, CellGroup, Field, Icon, Button } from "vant";
export default {
  data() {
    return {
      company: "",
      busines: "",
      startTime: dayjs(dayjs().format("YYYY-01")),
      endTime: "",
      logoUrl: "",

      // status
      isShowStartTime: false,
      isShowEndTime: false,

      oldTitle: "",
      currentTitle: "工作经历"
    };
  },
  components: {
    CellGroup,
    Cell,
    Field,
    datePicker,
    Icon,
    MyButton: Button
  },
  methods: {
    hideStartTime() {
      this.isShowStartTime = false;
    }
  },
  created() {
    this.oldTitle = document.title;
    document.title = currentTitle;
  },
  destroyed() {
    document.title = this.oldTitle;
  }
};
</script>

<style lang="postcss" scoped>
.resume-new-education {
  overflow-x: hidden;
  overflow-y: auto;
  &__item {
    margin-top: 10px;
  }

  .textarea {
    >>> .van-cell__title {
      display: none;
    }
  }
  .upload-img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }

  &__button {
    margin: 25px 12.5px 0;
  }
}
</style>



